

































var loginRes, _self;
export default {
	data() {
		return {
			//2个按钮的宽度
			btn1Width : 70,
			btn2Width : 70,
			//2个按钮的总宽 = btn1Width + btn23Width【uni-app 模板内不建议复杂运算请手动配置】
			deleteBtnWidth : 140,
			//正在滑动的索引
			scrollIndex : -1,
			//正在删除的索引
			deleteIndex : -1,
			tabCurrentIndex: 0,
			swiperCurrentIndex: 0,
			tabs: [{
					name: '公开文章',
					id: 'open'
				},
				{
					name: '私密文章',
					id: 'personal'
				}
			],
			titleShowId: 'tabTag-0',
			storageData: {
				userId: 0,
				nickname: '',
				avatar: '',
				login: false
			},
			articles: []
		};
	},
	onLoad: function() {
		_self = this;
	},
	onShow: function() {
		var _this = this;
		const loginKey = uni.getStorageSync('login_key');
		if (loginKey) {
			console.log(loginKey);
			this.storageData = {
				login: loginKey.login,
				nickname: loginKey.nickname,
				avatar: loginKey.avatar,
				userId: loginKey.userId
			};
			uni.request({
				url: 'http://localhost:8080/api/user/' + uni.getStorageSync('login_key').userId,
				method: 'GET',
				header: {
					'content-type': 'application/json'
				},
				success: res => {
					if (res.data.code === 0) {
						console.log(res.data.data.avatar + '————————————');
						_this.avatar = res.data.data.avatar;
						_this.nickname = res.data.data.nickname;
					}
				}
			});
			uni.request({
				url: this.apiServer + '/article/user',
				method: 'GET',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					uId: this.storageData.userId
				},
				success: res => {
					_this.articles = res.data.data;
				}
			});
		} else {
			this.storageData = {
				login: false
			};
		}

	},
	methods: {
		tabChange: function(e) {
			var index = e.target.id.replace('tabTag-', '');
			this.swiperCurrentIndex = index;
			this.tabCurrentIndex = index;
			this.titleShowId = 'tabTag-' + index;
		},
		swiperChange: function(e) {
			var index = e.detail.current;
			this.tabCurrentIndex = index;
			this.titleShowId = 'tabTag-' + index;
		},
		gotoDetail: function(aId) {
			uni.navigateTo({
				url: '../article_detail/article_detail?aId=' + aId + '&userId=' + this.storageData.userId
			});
		},
		removeMsg : function(e){
			var index = e.target.dataset.id;
			//删除确认
			if (this.deleteIndex != index){
				this.deleteIndex = index;
				setTimeout(function(){
					_self.deleteIndex = -1;
				}, 2000);
				return false;
			}
			this.scrollIndex = -1;
			setTimeout(function(){
				_self.msg.splice(index, 1);
				_self.deleteIndex = -1;
			}, 500);
		},
		touchStart: function (e) {
			x = e.mp.changedTouches[0].clientX;
			y = e.mp.changedTouches[0].clientY;
		},
		touchEnd : function(e){
			x = x - e.mp.changedTouches[0].clientX;
			y = y - e.mp.changedTouches[0].clientY;
			if (Math.abs(x) < 50) { return; }
			if (Math.abs(x) < Math.abs(y)) { return; }
			var index = e.currentTarget.dataset.id;
			if (index == this.scrollIndex){return ;}
			_self.scrollIndex = index;
		},
		changStatus : function(e){
			var index = e.currentTarget.dataset.id;
			this.msg[index].status = "已读";
		}
	},
};
